.report {
	&__slider {
		@include adaptiv-value(margin-bottom, 40, 20, 1);
	}

	&__wrapper-mini {
		gap: 5px;
	}

	&__slide-mini {
		@include adaptiv-value(width, 95, 60, 1);
		@include adaptiv-value(height, 95, 60, 1);
		opacity: 0.85;

		img {
			min-height: 100%;
			object-fit: cover;
		}

		&.swiper-slide-thumb-active {
			opacity: 1;
		}
	}

	&__slide,
	&__slide-mini {
		img {
			border-radius: 20px;
		}
	}

	&__swiper {
		margin-bottom: 5px;
	}

	&__about {
		display: flex;
		flex-direction: column;
	}

	&__about-title {
		font-weight: 700;
		font-size: 32px;
		line-height: 100%;
		color: var(--text-black);
		@media screen and (max-width: 835px) {
			font-size: 25px;
			line-height: 130%;
		}
	}

	&__about-price {
		display: flex;
		font-weight: 700;
		font-size: 25px;
		line-height: 100%;
		color: var(--primary-color);
		@include adaptiv-value(margin-top, 15, 10, 1);
		@include adaptiv-value(margin-bottom, 30, 15, 1);
	}

	&__about-price-total {
		margin-right: 25px;
	}

	// &__about-price-month {
	// }

	&__charact {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 15px;

		@media screen and (max-width: 835px) {
			grid-template-columns: repeat(1, 1fr);
		}
	}

	&__charact-column {
		display: flex;
		flex-direction: column;
		grid-column: 1 span;
		row-gap: 15px;
	}

	&__charact-row {
		display: flex;
		font-size: 18px;
		line-height: 100%;

		@media screen and (max-width: 835px) {
			justify-content: space-between;
		}
	}

	&__about {
		& .report__charact {
			@include adaptiv-value(column-gap, 60, 15, 1);
		}

		& .report__charact-column:nth-child(1) {
			& .report__charact-name {
				width: 103px;
			}
		}

		& .report__charact-column:nth-child(2) {
			& .report__charact-name {
				width: 92px;
			}
		}

		& .report__charact-row {
			@include adaptiv-value(column-gap, 35, 5, 1);
		}
		@media screen and (max-width: 835px) {
			& .report__charact-name {
				width: auto;
			}
		}
	}

	&__charact-name {
		grid-column: 1 span;
		font-weight: 400;
		color: var(--text-grey);
	}

	&__charact-data {
		grid-column: 1 span;
		font-weight: 500;
		color: var(--text-black);
		@media screen and (max-width: 790px) {
			text-align: right;
		}
	}

	&__about-btn-red {
		@include adaptiv-value(margin-top, 35, 25, 1);
		@include adaptiv-value(margin-bottom, 15, 10, 1);
	}

	&__info {
		@include adaptiv-value(margin-top, 55, 50, 1);
		@include adaptiv-value(margin-bottom, 60, 50, 1);
		display: flex;
		flex-direction: column;

		& .report__charact {
			@include adaptiv-value(margin-top, 40, 30, 1);
			@include adaptiv-value(margin-bottom, 15, 10, 1);
			@include adaptiv-value(column-gap, 35, 15, 1);
		}

		& .report__charact-column:nth-child(1) {
			& .report__charact-name {
				width: 119px;
			}
		}

		& .report__charact-column:nth-child(2) {
			& .report__charact-name {
				width: 156px;
			}
		}

		& .report__charact-row {
			@include adaptiv-value(column-gap, 20, 5, 1);
		}
		@media screen and (max-width: 835px) {
			& .report__charact-name {
				width: auto;
			}
		}
	}

	&__info-title {
		color: var(--text-black);
		font-size: 32px;
		font-weight: 700;
		line-height: 115%;
		@media screen and (max-width: 835px) {
			font-size: 29px;
			line-height: 115%;
		}
	}

	&__info-star {
		font-weight: 400;
		font-size: 18px;
		line-height: 100%;
		color: var(--text-grey);
	}

	&__info-data {
		display: flex;
		flex-direction: column;
		@include adaptiv-value(row-gap, 20, 15, 1);
		margin-top: 27px;
		@include adaptiv-value(margin-bottom, 20, 15, 1);
	}

	&__info-block {
		position: absolute;
		border: 1px solid var(--border-color);
		border-radius: 20px;
		padding: 25px;
		opacity: 0;
		visibility: hidden;
		transform: translateY(-20px);

		&.isVisible {
			position: static;
			opacity: 1;
			visibility: visible;
			transform: translateY(0);
		}
	}

	&__info-block-title {
		position: relative;
		margin-bottom: 10px;
		padding-left: 18px;
		font-weight: 700;
		font-size: 20px;
		line-height: 115%;
		color: var(--text-black);

		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 7.5px;
			display: block;
			border-radius: 50%;
			width: 8px;
			height: 8px;
			background-color: #00cb5d;
		}
	}

	&__info-block-text {
		font-weight: 400;
		font-size: 17px;
		line-height: 140%;
		color: var(--text-grey);
	}

	&__info-btn {
		display: flex;
		justify-content: center;
	}

	&__form {
		display: flex;
		flex-direction: column;
		gap: 0;
	}

	&__form-title {
		font-weight: 700;
		@include adaptiv-value(font-size, 32, 29, 1);
		line-height: 115%;
		color: var(--text-black);
	}

	&__form-input {
		display: grid;
		column-gap: 21px;
		grid-template-columns: repeat(2, 1fr);
		margin-bottom: 15px;
		row-gap: 15px;
		@include adaptiv-value(margin-top, 33, 38, 1);
		@media screen and (max-width: 650px) {
			grid-template-columns: repeat(1, 1fr);
		}
	}

	&__map {
		display: flex;
		flex-direction: column;
	}

	&__map-title {
		@include adaptiv-value(margin-top, 60, 50, 1);
		margin-bottom: 25px;
		font-weight: 700;
		@include adaptiv-value(font-size, 32, 29, 1);
		line-height: 115%;
		color: var(--text-black);
	}

	&__map-block {
		display: flex;
		overflow: hidden;
		border-radius: 20px;
		width: 100%;
		height: 368px;
	}
}

.ymaps-2-1-79-copyright {
	display: none !important;
}

